/*
Notice messages (like 'Added to cart', 'Billing address needs to be filled in', etc.
*/
.woocommerce-message,
.woocommerce-error,
.woocommerce-info {
  background-color: rgba(176, 176, 176, 0.6);
  color: #222;
  border-top-color: var(--wp--preset--color--primary);
  border-top-style: solid;
  border-top-width: 2px;
  padding: 1rem 1.5rem 1rem 3.5rem;
  margin-bottom: 2rem;
  list-style: none;
  font-size: var(--wp--preset--font-size--small);
  position: relative;
  *zoom: 1;
}
.woocommerce-message::before, .woocommerce-message::after,
.woocommerce-error::before,
.woocommerce-error::after,
.woocommerce-info::before,
.woocommerce-info::after {
  content: " ";
  display: table;
}
.woocommerce-message::after,
.woocommerce-error::after,
.woocommerce-info::after {
  clear: both;
}
.woocommerce-message[role=alert]::before,
.woocommerce-error[role=alert]::before,
.woocommerce-info[role=alert]::before {
  background: #d5d5d5;
  color: black;
  border-radius: 5rem;
  font-size: 1rem;
  content: "\e028";
  display: inline-block;
  margin-right: 1rem;
  height: 1.5em;
  line-height: 1.5em;
  text-align: center;
  width: 1.5em;
  position: absolute;
  top: 1em;
  left: 1.5em;
}
.woocommerce-message a.button,
.woocommerce-error a.button,
.woocommerce-info a.button {
  margin-bottom: -0.5rem;
  margin-top: -0.5rem;
  border: none;
  padding: 0.5rem 1rem;
}

.woocommerce-error[role=alert]::before {
  content: "X";
}

.woocommerce-message[role=alert]::before {
  content: "✓";
}